.tarifs {
    text-align: center;

    h1 {
        margin: 2rem;
    }

    .bulleContainer {
        // background-image: url('/imageImport/cochon_euro.jpg'); 
        display: flex;
        font-size: 18px;
        margin: 4rem;
        justify-content: space-around;
        .bullePrix {
            background-image: url('/imageImport/cochon_euro.jpg'); 
            background-position: center;
            background-size: cover;
            -webkit-text-stroke: 1px black; 
            text-shadow: 6px 4px 6px black; 
            letter-spacing: 5px; 
            font-weight: 800;
            color: white;
            font-size: 18px;  
            cursor: pointer;
            text-align: center;
            display: flex;
            align-items: center;
            align-content: center;
            height: 200px; 
            width: 200px;
            border-radius: 50%;
            margin: 3rem; 
        }
    }
    .devis {
        margin: 1rem;
        font-size: 18px;
    }
    .tva {
        margin: 2rem;
        font-size: 18px;
        text-decoration: underline;
    }
    .prestation {
        height: 140px; 
        margin: 1rem;
        text-align: left;
        font-size: 18px;
        .title {
            margin: 1rem;
        }
        .prestaList {
            margin: 1rem;
        }
    }     
}
