.cardTxt{
    display: flex;
    padding: 25px;
    justify-content: space-around;
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(131,126,126,1) 52%, rgba(255,255,255,1) 100%);
    .border{
        border-left: 2px solid black;
        margin: 0 1%;
    }
    .cardListTxt {
        margin: 50px 0;
        font-size: 25px;
        h2{
            font-size: 38px;
            text-shadow: 6px 4px 8px black;
        }
        ul{
            margin: 25px;
        }
    }
}